.sun {
  height: 50px;
  background-image: url(img/sun.png);
  background-repeat: no-repeat; }

.wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100vh;
  background: #333333;
  background: -webkit-linear-gradient(45deg, #333333 0%, #222222 100%);
  background: -o-linear-gradient(45deg, #333333 0%, #222222 100%);
  background: linear-gradient(45deg, #333333 0%, #222222 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#333333', endColorstr='#222222',GradientType=1 ); }

.loading {
  background-image: url(../src/img/sunny.svg);
  background-repeat: no-repeat;
  opacity: .1;
  text-transform: uppercase;
  -webkit-animation: turning 4s infinite;
          animation: turning 4s infinite; }

@-webkit-keyframes turning {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    width: 130px;
    height: 130px; }
  50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    width: 140px;
    height: 140px; }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    width: 130px;
    height: 130px; } }

@keyframes turning {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    width: 130px;
    height: 130px; }
  50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    width: 140px;
    height: 140px; }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    width: 130px;
    height: 130px; } }

.logo {
  color: #fff;
  height: 100px;
  width: 100px;
  background-repeat: no-repeat;
  background-size: cover; }
  .logo__sunny {
    background-image: url(../src/img/sunny.svg); }
  .logo__rain {
