  .logo__snow {
    background-image: url(../src/img/snow.svg); }
  .logo__moon {
    background-image: url(../src/img/moon.svg); }

.city {
  margin-top: 5px;
  font-size: 30px;
  text-transform: uppercase;
  color: #fff; }

.temp {
  margin-top: 5px;
  text-align: center;
  font-size: 50px; }
  .temp__rain {
    color: #50d1ef; }
  .temp__sunny {
    color: #ffeb3b; }
  .temp__snow {
    color: #80c7ea; }
  .temp__moon {
    color: #384e64; }



